import React from "react"
import {Link} from "react-router-dom"
import styles from "../styles/NavBar.css"
import {Helmet} from "react-helmet";
import logo from "../resources/logo.jpg";

function NavBar(){



    return (
        <nav className="nav">
            <div className="logo-container">
                <img src={logo} alt="Logo" className="logo" style={{padding:5}}/>
                <div className="title" style={{padding:5}}>Computer Chapter</div>
            </div>
            <Link to="#" class="toggle">
            <span class="bar"></span>
            <span class="bar"></span>
            <span class="bar"></span>
            </Link>
            <div >
            <ul className="navbar" id="#navb">
                <li>
                    <Link to="/">Home</Link>    
                </li>
                <li>
                    <Link to="/events">Events</Link>
                    <ul className="nav-dropdown">
                        <li>
                            <Link to="/">e</Link>    
                        </li>
                        <li>
                            <Link to="/">e</Link>    
                        </li>
                        <li>
                            <Link to="/">e</Link>    
                        </li>
                    </ul>

                </li>
                <li>
                    <Link to="/team">Team
                    </Link>
                </li>
                <li>
                    <Link to="/calendar">Calendar</Link>
                </li>
                </ul>
            </div>
            <Helmet>
                <script>
                {
                    `const toggleMenu = document.getElementsByClassName("toggle")[0]

                    const navbarLinks = document.getElementsByClassName("navbar")[0]

                    toggleMenu.addEventListener("click", () => {
                        navbarLinks.classList.toggle("active")
                    })` 
                }
                </script>
            </Helmet>

        </nav>
    )
}

export default NavBar